() {\n      this.$refs.uploadInput.click();\n    },\n    readAsDataURL(file) {\n      return new Promise(function (resolve, reject) {\n        let fr = new FileReader();\n        fr.onload = function () {\n          resolve(fr.result);\n        };\n        fr.onerror = function () {\n          reject(fr);\n        };\n        fr.readAsDataURL(file);\n      });\n    },\n    deleteImg(index) {\n      this.Imgs.splice(index, 1);\n      this.files.splice(index, 1);\n      this.$emit(\"changed\", this.files);\n      this.$refs.uploadInput.value = null;\n    },\n    previewImgs(event) {\n      if (\n        this.$props.max &&\n        event &&\n        event.currentTarget.files.length + this.files.length > this.$props.max\n      ) {\n        this.error = this.$props.maxError\n          ? this.$props.maxError\n          : `Maximum files is` + this.$props.max;\n        return;\n      }\n      if (this.dropped == 0) this.files.push(...event.currentTarget.files);\n      this.error = \"\";\n      this.$emit(\"changed\", this.files);\n      let readers = [];\n      if (!this.files.length) return;\n      for (let i = 0; i < this.files.length; i++) {\n        readers.push(this.readAsDataURL(this.files[i]));\n      }\n      Promise.all(readers).then((values) => {\n        this.Imgs = values;\n      });\n    },\n    reset() {\n      this.$refs.uploadInput.value = null;\n      this.Imgs = [];\n      this.files = [];\n      this.$emit(\"changed\", this.files);\n    },\n  },\n};\n</script>\n\n<template>\n    <div\n        class=\"container mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-gray-300 border-dashed rounded-md\"\n        @dragover.prevent=\"dragOver\"\n        @dragleave.prevent=\"dragLeave\"\n        @drop.prevent=\"drop($event)\"\n    >\n    <div class=\"drop\" v-show=\"dropped == 2\"></div>\n    <!-- Error Message -->\n    <div v-show=\"error\" class=\"error\">\n      {{ error }}\n    </div>\n\n    <!-- To inform user how to upload image -->\n    <div v-show=\"Imgs.length == 0\" class=\"beforeUpload\">\n        <input\n            type=\"file\"\n            style=\"z-index: 1\"\n            accept=\"image/*\"\n            ref=\"uploadInput\"\n            @change=\"previewImgs\"\n            multiple\n        />\n      <svg class=\"icon\" xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 24 24\">\n        <title>Upload Image</title>\n        <g id=\"Upload_Image\" data-name=\"Upload Image\">\n          <g id=\"_Group_\" data-name=\"&lt;Group&gt;\">\n            <g id=\"_Group_2\" data-name=\"&lt;Group&gt;\">\n              <g id=\"_Group_3\" data-name=\"&lt;Group&gt;\">\n                <circle\n                  id=\"_Path_\"\n                  data-name=\"&lt;Path&gt;\"\n                  cx=\"18.5\"\n                  cy=\"16.5\"\n                  r=\"5\"\n                  style=\"\n                    fill: none;\n                    stroke: #303c42;\n                    stroke-linecap: round;\n                    stroke-linejoin: round;\n                  \"\n                />\n              </g>\n              <polyline\n                id=\"_Path_2\"\n                data-name=\"&lt;Path&gt;\"\n                points=\"16.5 15.5 18.5 13.5 20.5 15.5\"\n                style=\"\n                  fill: none;\n                  stroke: #303c42;\n                  stroke-linecap: round;\n                  stroke-linejoin: round;\n                \"\n              />\n              <line\n                id=\"_Path_3\"\n                data-name=\"&lt;Path&gt;\"\n                x1=\"18.5\"\n                y1=\"13.5\"\n                x2=\"18.5\"\n                y2=\"19.5\"\n                style=\"\n                  fill: none;\n                  stroke: #303c42;\n                  stroke-linecap: round;\n                  stroke-linejoin: round;\n                \"\n              />\n            </g>\n            <g id=\"_Group_4\" data-name=\"&lt;Group&gt;\">\n              <polyline\n                id=\"_Path_4\"\n                data-name=\"&lt;Path&gt;\"\n                points=\"0.6 15.42 6 10.02 8.98 13\"\n                style=\"\n                  fill: none;\n                  stroke: #303c42;\n                  stroke-linecap: round;\n                  stroke-linejoin: round;\n                \"\n              />\n              <polyline\n                id=\"_Path_5\"\n                data-name=\"&lt;Path&gt;\"\n                points=\"17.16 11.68 12.5 7.02 7.77 11.79\"\n                style=\"\n                  fill: none;\n                  stroke: #303c42;\n                  stroke-linecap: round;\n                  stroke-linejoin: round;\n                \"\n              />\n              <circle\n                id=\"_Path_6\"\n                data-name=\"&lt;Path&gt;\"\n                cx=\"8\"\n                cy=\"6.02\"\n                r=\"1.5\"\n                style=\"\n                  fill: none;\n                  stroke: #303c42;\n                  stroke-linecap: round;\n                  stroke-linejoin: round;\n                \"\n              />\n              <path\n                id=\"_Path_7\"\n                data-name=\"&lt;Path&gt;\"\n                d=\"M19.5,11.6V4A1.5,1.5,0,0,0,18,2.5H2A1.5,1.5,0,0,0,.5,4V15A1.5,1.5,0,0,0,2,16.5H13.5\"\n                style=\"\n                  fill: none;\n                  stroke: #303c42;\n                  stroke-linecap: round;\n                  stroke-linejoin: round;\n                \"\n              />\n            </g>\n          </g>\n        </g>\n      </svg>\n\n    <div class=\"flex text-sm text-gray-600\">\n        <label for=\"file-upload mainMessage\">\n            <span>{{ uploadMsg ? uploadMsg : \"Upload a file or drag and drop\" }}</span>\n            <input id=\"file-upload\" name=\"file-upload\" type=\"file\" class=\"sr-only\"/>\n        </label>\n    </div>\n    </div>\n    <div class=\"imgsPreview\" v-show=\"Imgs.length > 0\">\n      <button type=\"button\" class=\"clearButton\" @click=\"reset\">\n        <span class=\"text-red-500\">{{ clearAll ? clearAll : \"Clear all\" }}</span>\n      </button>\n      <div class=\"imageHolder\" v-for=\"(img, i) in Imgs\" :key=\"i\">\n        <img :src=\"img\" />\n        <span class=\"delete\" style=\"color: white\" @click=\"deleteImg(--i)\">\n          <svg\n            class=\"icon\"\n            xmlns=\"http://www.w3.org/2000/svg\"\n            fill=\"none\"\n            viewBox=\"0 0 24 24\"\n            stroke=\"currentColor\"\n          >\n            <path\n              stroke-linecap=\"round\"\n              stroke-linejoin=\"round\"\n              stroke-width=\"2\"\n              d=\"M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16\"\n            />\n          </svg>\n        </span>\n        <div class=\"plus\" @click=\"append\" v-if=\"++i == Imgs.length\">+</div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n.container {\n  width: 100%;\n  height: 100%;\n  background: #f7fafc;\n  border: 2px dashed #bcbfc4;\n  border-radius: 10px;\n  /* padding: 28px; */\n  position: relative;\n}\n.drop {\n  width: 100%;\n  height: 100%;\n  top: 0;\n  border-radius: 10px;\n  position: absolute;\n  background-color: #f4f6ff;\n  left: 0;\n  border: 2px dashed #a3a8b1;\n}\n.error {\n  text-align: center;\n  color: red;\n  font-size: 15px;\n}\n.beforeUpload {\n  position: relative;\n  text-align: center;\n}\n.beforeUpload input {\n  width: 100%;\n  margin: auto;\n  height: 100%;\n  opacity: 0;\n  position: absolute;\n  background: red;\n  display: block;\n}\n.beforeUpload input:hover {\n  cursor: pointer;\n}\n.beforeUpload .icon {\n  width: 150px;\n  margin: auto;\n  display: block;\n}\n.imgsPreview .imageHolder {\n  width: 150px;\n  height: 150px;\n  background: #fff;\n  position: relative;\n  border-radius: 10px;\n  margin: 5px 5px;\n  display: inline-block;\n}\n.imgsPreview .imageHolder img {\n  object-fit: cover;\n  width: 100%;\n  height: 100%;\n}\n.imgsPreview .imageHolder .delete {\n  position: absolute;\n  top: 4px;\n  right: 4px;\n  width: 29px;\n  height: 29px;\n  color: #fff;\n  background: red;\n  border-radius: 50%;\n}\n.imgsPreview .imageHolder .delete:hover {\n  cursor: pointer;\n}\n.imgsPreview .imageHolder .delete .icon {\n  width: 66%;\n  height: 66%;\n  display: block;\n  margin: 4px auto;\n}\n.imgsPreview .imageHolder .plus {\n  color: #2d3748;\n  background: #f7fafc;\n  border-radius: 50%;\n  font-size: 21pt;\n  height: 30px;\n  width: 30px;\n  text-align: center;\n  border: 1px dashed;\n  line-height: 23px;\n  position: absolute;\n  right: -42px;\n  bottom: 43%;\n}\n.plus:hover {\n  cursor: pointer;\n}\n.clearButton {\n  color: #2d3748;\n  position: absolute;\n  top: 7px;\n  right: 7px;\n  background: none;\n  border: none;\n  cursor: pointer;\n}\n</style>"],"sourceRoot":""}]);
